@import url(https://fonts.googleapis.com/css?family=Source+Serif+Pro&display=swap);
div#palette {
  display: block;
  height: 150px;
  border: 2px solid lightgray;
  border-radius: 10px 10px 0 0;
}

div#label {
  border: 2px solid lightgray;
  border-radius: 0 0 10px 10px;
  margin-bottom: 20px;
}

div#color {
  -webkit-column-break-inside: avoid;
     -moz-column-break-inside: avoid;
          break-inside: avoid-column;
  width: 150px;
}

p {
  padding-left: 10px;
}



div#filteredPalette {
  display: block;
  height: 75px;
  border: 2px solid lightgray;
  border-radius: 10px 10px 0 0;
}

div#filteredLabel {
  border: 2px solid lightgray;
  border-radius: 0 0 10px 10px;
}

p {
  padding-left: 5px;
}

div#filteredColor {
  width: 125px;
  margin-bottom: 10px;
  -webkit-column-break-inside: avoid;
     -moz-column-break-inside: avoid;
          break-inside: avoid-column;
}

div#detailPalette {
  display: block;
  height: 400px;
  width: 690px;
  border: 1px solid black;
  border-radius: 10px 10px 0 0;
}

div#detailLabel {
  border: 2px solid lightgray;
  border-radius: 0 0 10px 10px;
  width: 690px;
  margin-bottom: 20px;
}

div#detailWrap {
  margin-top: 70px;
  margin-left: 300px;
}

div#filterWrap {
  -webkit-columns: 5 auto;
     -moz-columns: 5 auto;
          columns: 5 auto;
  -webkit-column-width: 150px;
     -moz-column-width: 150px;
          column-width: 150px;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
     -moz-column-gap: 30px;
          column-gap: 30px;
  -webkit-column-break-inside: avoid;
     -moz-column-break-inside: avoid;
          break-inside: avoid-column;
  margin-top: 30px;
}
div#pageNumbers {
  vertical-align: baseline;
  display: inline-block;
}

h4{
  margin-left: 20px;
  margin-right: 20px;
}

h4#selectedPage {
  font-weight: bold;
  text-decoration: underline;
}
div#wrap {
  -webkit-columns: 4 auto;
     -moz-columns: 4 auto;
          columns: 4 auto;
  -webkit-column-width: 150px;
     -moz-column-width: 150px;
          column-width: 150px;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
     -moz-column-gap: 40px;
          column-gap: 40px;
  -webkit-column-break-inside: avoid;
     -moz-column-break-inside: avoid;
          break-inside: avoid-column;
  margin-top: 70px;
}

div#pages {
  margin-bottom: 10px; 
  margin-left: 350px;
  width: 50%;
}
div#header {
  background-color: darkslategray;
  width: 100%;
  height: 100px;
}

img {
  padding-left: 25px;
  padding-top: 20px;
}
div#sidebar {
height: 600px;
width: 215px;
float:left;
background-color: lightgray;
border: solid gray 2px;
margin-right: 20px;
}

div#colors {
  margin-left: 25px;
}

button {
  margin-top: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 40px;
  width: 190px;
  border: solid black 1px;
  background-color: white;
  border-radius: 7px 7px 7px 7px;
}
div {
  font-family: 'Source Serif Pro', serif;
}

